
.Heading{
    margin: auto;
    width: 35%;
    border-bottom: 4px solid #dc143c;
}
.liveIcons{
    width: 40px;
    height: 40px;
    background-color:transparent;
    border-radius: 50%;
    display: flex;
 justify-content: center;
    align-items:center ;
    cursor: pointer;
}

.liveIcons:hover{
    width: 40px;
    height: 40px;
    background-color: #dc143c;
    border-radius: 50%;
    display: flex;
 justify-content: center;
    align-items:center ;
}
